@import '../global.scss';
@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@300;400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@300;400;500&display=swap');
@import url('https://fonts.googleapis.com/css?family=Raleway:300i,400,500,700|Roboto:400,500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap');

body {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
}
a:link {
  text-decoration: none;
}
a:-webkit-any-link {
    color: black;
    cursor: pointer;
    text-decoration: none;
}
@keyframes openSesame {
  from {
    width: 20%;
  }
  to {
    width: 70%;
  }
}
@media (min-width: 480px){
  @keyframes openSesame {
    from {
    width: 20%;
    }
    to {
    width: 70%;
    }
  }
}  
@media (min-width: 768px){
  @keyframes openSesame {
    from {
    width: 20%;
    }
    to {
    width: 65%;
    }
  }
}
@media (min-width: 992px){
  @keyframes openSesame {
    from {
    width: 20%;
    }
    to {
    width: 65%;
    }
  }
}
@media (min-width: 1200px){
 @keyframes openSesame {
  from {
    width: 30%;
  }
  to {
    width: 55%;
  }
  }
}

@mixin projectTitles($background, $font) {
  background-color: $background;
  border: 4px solid $col2d;
  color: $font;
  padding-left: 20%;
  padding-right: 20%;
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  animation-name: fadeIn;
  &:hover {
    border: 7px solid $col2d;
  }
  &:active {
    border: 7px solid black;
  }
}

.footer {
  font-size: 0.7rem;
  margin-top: 50px;
  height: 50px;
  background-color: $col2c;
  color: $col1a;
}
#contact_container {
  display: flex;
  font-size: 0.7rem;
  margin-bottom: 30px;
  margin-top: 60px;
  justify-content: center;
  @media (min-width: 480px){
      margin-bottom: 30px;
      margin-top: 100px;
      height: 80px;
      font-size: 0.8rem;
    }
    @media (min-width: 768px){
      margin-top: 140px;
      margin-bottom: 30px;
      height: 100px;
    }
    @media (min-width: 992px){
      font-size: 0.9rem;
    }
    @media (min-width: 1200px){
    }
}
#contact_button {
  display: flex;
  flex-direction: row;
  border: 2px solid black;
  justify-content: left;
  align-items: center;
  width: auto;
  height: 40%;
  padding: 10px;
  border-radius: 5px;
  background-color: rgb(250, 247, 241);
   &:hover {
    font-size:0.71rem;
    background-color: white;
    }
    &:active {
    border: 2px solid white;
  }
  @media (min-width: 480px){
    height: 30px;
    &:hover {
    font-size:0.81rem;
    background-color: white;
    }
    &:active {
    border: 2px solid white;
    }
  }
  @media (min-width: 992px){
    height: 40px;
    &:hover {
    font-size:0.91rem;
    background-color: white;
    }
    &:active {
    border: 2px solid white;
    }
  }
}

.linkedin {
  margin: 0px 10px 0px 0px;
  height: 18px;
  &:hover {
    filter: invert(100%);
  }
  &#top {
    margin: 0px 25px 0px 0px;
  }
  @media (min-width: 480px){
    height: 30px;
    &#top {
    margin: 0px 32px 0px 0px;
    }
  }
  @media (min-width: 768px){
    height: 35px;
    &#top {
    margin: 0px 46px 0px 0px;
    }
  }
  @media (min-width: 992px){
    height: 40px;
    &#top {
    margin: 0px 60px 0px 0px;
    }
  }
  @media (min-width: 1200px){
    &#top {
    margin: 0px 80px 0px 40px;
    }
  }
  @media (min-width: 1500px){
    &#top {
    margin: 0px 90px 0px 100px;
    }
  }
  @media (min-width: 2000px){
    &#top {
    margin: 0px 100px 0px 250px;
    }
  }
}
.main_container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.header_container {
  display: flex;
  justify-content: center;
  width: 100%;
  background-image: linear-gradient($col1c, $col2b, $col2b);

  .header_content{
    display: flex;
    flex-direction: row;
    // align-self: top;
    justify-content: space-between;
    width: 100%;
    background-color: $col2a;
    margin-top: 8px;
    border-radius: 60px 25px 0px 0px;
    @media (min-width: 480px){
      width: 95%;
      margin-top: 10px;
      border-radius: 90px 36px 0px 0px;
    }
    @media (min-width: 768px){
      width: 90%;
      margin-top: 15px;
      border-radius: 140px 45px 0px 0px;
    }
    @media (min-width: 992px){
      width: 86%;
      margin-top: 20px;
      border-radius: 160px 60px 0px 0px;
    }
    @media (min-width: 1200px){
      width: 85%;
      border-radius: 165px 80px 0px 0px;
    }
    .face {
      border-radius: 45px 3px;
      width: 100px;
      height: 100px;
      margin: 15px;
      @media (min-width: 480px){
        border-radius: 60px 10px;
        width: 150px;
        height: 150px;
        margin: 25px 18px 18px 25px;
      }
      @media (min-width: 768px){
        border-radius: 90px 20px;
        width: 200px;
        height: 200px;
        margin: 30px;
      }
      @media (min-width: 992px){
        border-radius: 110px 25px;
        width: 230px;
        height: 230px;
        margin: 38px;
      }
      @media (min-width: 1200px){
        border-radius: 120px 30px;
        width: 260px;
        height: 260px;
        margin: 40px;
        // margin-bottom: 100px;
      }
    }
    .text_box {
      max-width: 90%;
      @media (min-width: 480px){
        max-width: 80%;
      }
      @media (min-width: 768px){
        max-width: 70%;
      }
      @media (min-width: 992px){
        max-width: 60%;
      }
      @media (min-width: 1200px){
        max-width: 60%;
      }
    }
    .greetings {
      color: $col2b;
      font-size: 1.1rem;
      margin-top: 15px;
      // font-weight: 400;
      @media (min-width: 480px){
        font-size: 1.3rem;
        margin-top: 25px;
      }
      @media (min-width: 768px){
        font-size: 1.8rem;
        margin-top: 40px;
      }
      @media (min-width: 992px){
        font-size: 2.2rem;
        margin-top: 52px;
      }
      @media (min-width: 1200px){
        font-size: 2.5rem;
        margin-top: 70px;
      }
    }
    .iam {
      color: whitesmoke;
      font-size: 0.6rem;
      margin: 12px 0px 20px;
      font-weight: 300;
      @media (min-width: 480px){
        font-size: 0.8rem;
      }
      @media (min-width: 768px){
        font-size: 1rem;
        margin-top: 20px;
        // max-width: 490px;
      }
      @media (min-width: 992px){
        font-size: 1.1rem;
        // max-width: 520px;
      }
      @media (min-width: 1200px){
        font-size: 1.3rem;
        // max-width: 650px;
      }
      @media (min-width: 1500px){
        // max-width: 750px;
        // font-weight: 400;
        
      }
    }
  }
}

