.project_container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: left;
  width: 100%;
  background-image: linear-gradient($col2d, $col2d);
  border-radius:0px 0px 25px 25px;
  @media (min-width: 480px){
    width: 95%;
  }
  @media (min-width: 768px){
    width: 90%;
  }
  @media (min-width: 992px){
    width: 86%;
  }
  @media (min-width: 1200px){
    width: 85%;
  }
}
.block {
  width: 100%;
  justify-content: center;
  display: flex;
  align-items: flex-end;
  animation-name: fadeIn;
  animation-duration: 1s;
  animation-timing-function: cubic-bezier(1,.22,.52,.96);
}
.project {
  display: flex;
  // margin: 40px;
  height: 220px;
  width: 72%;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  border-radius: 20px;
  margin-top: 30px;
  @media (min-width: 480px){
    margin-top: 70px;
    width: 70%;
    height: 280px;
  }
  @media (min-width: 768px){
    margin-top: 90px;
    width: 65%;
    height: 320px;
  }
  @media (min-width: 992px){
    margin-top: 100px;
    width: 65%;
    height: 380px;
  }
  @media (min-width: 1200px){
    margin-top: 100px;
    width: 55%;
    max-width: 760px;
    height: 400px;
  }
}

