    animation-name: fadeIn;
    animation-timing-function: ease-in;
    z-index: 3;
    @media (min-width: 480px){
      font-size: 1.3rem;
    }
    @media (min-width: 768px){
      font-size: 1.5rem;
    }
    @media (min-width: 992px){
      font-size: 1.6rem;
    }
    @media (min-width: 1200px){
      font-size: 2rem;
    }
  }
.subtitle {
    text-align: center;
    margin-top: 3px;
    margin-bottom: 20px;
    width: 40%;
    font-size: 0.6rem;
    color: $col2d;
    animation-duration: 4s;
    animation-name: fadeIn;
    animation-timing-function: ease-in;
    z-index: 3;
